<template>
  <div class="container py-5">
    <form @submit.stop.prevent="handleSubmit">
      <div class="form-group">
        <label for="name">Name</label>
        <input id="name" type="text" name="name" class="form-control" placeholder="Enter Name" v-model="profile.name"
          required>
      </div>

      <div class="form-group">
        <label for="image">Image</label>
        <img v-if="profile.image" :src="profile.image" class="d-block img-thumbnail mb-3" width="200" height="200">
        <input id="image" type="file" name="image" accept="image/*" class="form-control-file"
          @change="handleFileChange">
      </div>

      <button :disabled="isProcessing" type="submit" class="btn btn-primary">
        {{ isProcessing ? "處理中..." : 'Submit' }}
      </button>
    </form>
  </div>
</template>

<script>
import usersAPI from './../apis/users'
import { Toast } from './../utils/helpers'
import { mapState } from 'vuex'

export default {
  data() {
    return {
      profile: {
        id: -1,
        name: '',
        image: ''
      },
      isProcessing: false
    }
  },
  computed: {
    ...mapState(['currentUser', 'isAuthenticated'])
  },
  created() {
    this.setUser()
  },
  beforeRouteUpdate(to, from, next) {
    this.setUser()
    next()
  },
  watch: {

  },
  methods: {
    setUser() {
      const { id, name, image } = this.currentUser
      this.profile = {
        ...this.profile,
        id,
        name,
        image
      }
    },
    handleFileChange(e) {
      const { files } = e.target

      if (files.length === 0) {
        // 使用者沒有選擇上傳的檔案
        this.profile.image = ''
      } else {
        // 否則產生預覽圖
        const imageURL = window.URL.createObjectURL(files[0])
        this.profile.image = imageURL
      }
    },
    async handleSubmit(e) {
      try {
        this.isProcessing = true
        const form = e.target
        const formData = new FormData(form)
        // 透過 API 將表單資料送到伺服器
        const response = await usersAPI.update({ userId: this.currentUser.id, formData })
        if(response.statusText !== 'OK') throw new Error(response.statusText)

        this.isProcessing = false
        this.$store.commit('setCurrentUser', { ...this.profile })
      } catch (error) {
        this.isProcessing = false
        Toast.fire({
          icon: 'error',
          title: 'Unable to edit user profile, Please try again'
        })
      }
    },
  }
}
</script>